.accordion {
    border-bottom: 1px solid var(--jiffyui-naturalLight-300);
}
.accordion-body.collapsed {
    padding: 0rem;
    max-height: 0rem;
    transition: max-height 0.35s cubic-bezier(0, 1, 0, 1);
}
.accordion-body {
    overflow: hidden;
    transition: max-height 0.3s cubic-bezier(1, 0, 1, 0);
    height: auto;
    max-height: 9999px;
}
.accordion-wrapper {
    margin-bottom: 0.4rem;
    border-radius: 0.4rem;
}
.accordion-header {
    padding: 1rem 0rem;
    cursor: pointer;
    font-size: 1.4rem;
    position: relative;
}
.accordion-header.accordion-left--icon {
    display: flex;
    gap: 1rem;
    align-items: center;
}
.accordion-header__title {
    line-height: 2.5rem;
    font-size: 1.4rem;
    font-family: var(--jiffyui-medium-font);
    color: var(--jiffyui-naturalDark-1300);
}
.accordion-body_content {
    padding: 1rem 0rem;
    font-size: 1.4rem;
    line-height: 2.2rem;
    color: var(--jiffyui-naturalDark-900);
}
.accordion-headaer__icon svg {
    display: block;
    color: var(--jiffyui-naturalDark-1300);
}
.accordion-header>svg {
    color: var(--jiffyui-naturalDark-1300);
    position: absolute;
    right: 0.1rem;
    top: 50%;
    transform: translateY(-50%) rotate(0deg);
    transition: 0.3s all;
}
.accordion-header.active>svg {
    transform: translateY(-50%) rotate(180deg);
}
.icon-align--left .accordion-header {
    padding-left: 3.5rem;
}
.accordion-wrapper.icon-align--left .accordion-header svg {
    left: 1.2rem;
}
.pixel-dropdown {
    border: 1px solid #6C849D2E;
    box-shadow: 0px 8px 24px 0px #1928391F;
    background-color: #ffffff;
    max-height: 25rem;
    overflow: auto;
    border-radius: 4px;
}
.jiffyui-actionlist__item {
    &:hover {
        background-color: rgba(108, 132, 157, 0.09);
        border-radius: 0.4rem;
        transition: 0.3s all;
    }
}

.jiffyui-actionlist__title {
    color: var(--jiffyui-naturalLight-500);
    font-family: var(--jiffyui-regular-font);
    display: block;
    padding: 0;
    border-bottom: 0.1rem solid var(--jiffyui-naturalLight-300);
    padding-bottom: 0.4rem;
    font-size: 1.2rem;
    margin-bottom: 0.2rem;
}

.jiffyui-actionlist_item__label {
    color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-actionlist_item__prefix__suffix {
    display: flex;
    gap: 0.8rem;
    align-items: start;
}

.jiffyui-actionlist_item_description {
    color: var(--jiffyui-naturalDark-900);
    font-size: 1.2rem;
}

.jiffyui-actionlist_item__prefix--icon svg,
.jiffyui-actionlist_item__suffix--icon svg {
    display: block;
    width: 1.5rem;
    height: 1.6rem;
    color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-actionlist__item--destructive .jiffyui-actionlist_item__prefix--icon svg {
    color: var(--jiffyui-Negative-600);
}

.jiffyui-actionlist__item--destructive .jiffyui-actionlist_item__label {
    color: var(--jiffyui-Negative-600);
}

.jiffyui-actionlist__item--destructive .jiffyui-actionlist_item__suffix--icon {
    svg {
        color: var(--jiffyui-Negative-600);
    }
}

.jiffyui-actionlist__item--destructive {
    &:hover {
        background-color: var(--jiffyui-Negative-050);
    }
}

.jiffyui-actionlist__item--disabled {
    cursor: not-allowed;
    opacity: 0.5;
}
.jiffyui-alert {
    padding: 1.6rem;
    border: var(--jiffyui-borderWidth-thin) solid var(--jiffyui-naturalLight-050);
    border-radius: 0.5rem;
    background: var(--jiffyui-white);
    position: relative;
}

.jiffyui-alert_inner {
    display: flex;
    gap: 1rem;
    align-items: flex-start;

}

.jiffyui-alert_content {
    display: flex;
    flex-direction: column;
}

.jiffyui-alert_tile {
    margin-top: 0;
    font-size: 1.6rem;
    font-family: var(--jiffyui-medium-font);
    margin-bottom: 0.4rem;
}

.jiffyui-alert_description {
    color: var(--jiffyui-naturalDark-1000);
}

.jiffyui-alert_actions {
    margin-top: 1.5rem;
    display: flex;
    align-items: center;
    gap: 2rem;
}

.jiffyui-alert {
    .pixel-textLink {
        a {
            font-family: var(--jiffyui-medium-font);
        }
    }
}

.jiffyui-alert--subtile {
    h3 {
        .jiffyui-alert_tile {
            color: var(--jiffyui-naturalDark-1300);
        }
    }
}

.jiffyui-alert--negative {
    &.jiffyui-alert--subtile {
        border-color: var(--jiffyui-Negative-200);
        background: var(--jiffyui-Negative-100);
        .btn {
            &.btn--secondry span {
                color: var(--jiffyui-Negative-500);
            }
        }
    }

    &.jiffyui-alert--subtile {
        .jiffyui-alert_icon {
            svg {
                color: var(--jiffyui-Negative-500);
            }
        }
    }
}

.jiffyui-alert--negative.jiffyui-alert--subtile .pixel-textLink a {
    color: var(--jiffyui-Negative-500);
}

.jiffyui-alert--possitive.jiffyui-alert--subtile {
    border-color: var(--jiffyui-Possitive-200);
    background: var(--jiffyui-Possitive-100);
}

.jiffyui-alert--possitive.jiffyui-alert--subtile .jiffyui-alert_icon svg {
    color: var(--jiffyui-Possitive-500);
}

.jiffyui-alert--possitive.jiffyui-alert--subtile .btn.btn--secondry {
    background: var(--jiffyui-Possitive-200);
    border-color: var(--jiffyui-Possitive-500);
}

.jiffyui-alert--possitive.jiffyui-alert--subtile .btn.btn--secondry span {
    color: var(--jiffyui-Possitive-500);
}

.jiffyui-alert--possitive.jiffyui-alert--subtile .pixel-textLink a {
    color: var(--jiffyui-Possitive-500);
}

.jiffyui-alert--waiting.jiffyui-alert--subtile {
    border-color: var(--jiffyui-Notice-200);
    background: var(--jiffyui-Notice-100);
}

.jiffyui-alert--waiting.jiffyui-alert--subtile .jiffyui-alert_icon svg {
    color: var(--jiffyui-Notice-500);
}

.jiffyui-alert--waiting.jiffyui-alert--subtile .btn.btn--secondry {
    background: var(--jiffyui-Notice-100);
    border-color: var(--jiffyui-Notice-400);
}

.jiffyui-alert--waiting.jiffyui-alert--subtile .btn.btn--secondry span {
    color: var(--jiffyui-Notice-500);
}

.jiffyui-alert--waiting.jiffyui-alert--subtile .pixel-textLink a {
    color: var(--jiffyui-Notice-400);
}

.jiffyui-alert--primary.jiffyui-alert--subtile {
    border-color: var(--jiffyui-Sapphire-200);
    background: var(--jiffyui-Sapphire-100);
}

.jiffyui-alert--primary.jiffyui-alert--subtile .jiffyui-alert_icon svg {
    color: var(--jiffyui-Sapphire-500);
}

.jiffyui-alert--primary.jiffyui-alert--subtile .btn.btn--secondry {
    background: var(--jiffyui-Sapphire-100);
    border-color: var(--jiffyui-Sapphire-500);
}

.jiffyui-alert--primary.jiffyui-alert--subtile .btn.btn--secondry span {
    color: var(--jiffyui-Sapphire-500);
}

.jiffyui-alert--primary.jiffyui-alert--subtile .pixel-textLink a {
    color: var(--jiffyui-Sapphire-500);
}

.jiffyui-alert--neutral.jiffyui-alert--subtile {
    border-color: var(--jiffyui-naturalLight-200);
    background: var(--jiffyui-naturalLight-100);
}

.jiffyui-alert--neutral.jiffyui-alert--subtile .jiffyui-alert_icon svg {
    color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-alert--neutral.jiffyui-alert--subtile .btn.btn--secondry {
    background: var(--jiffyui-naturalLight-200);
    border-color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-alert--neutral.jiffyui-alert--subtile .btn.btn--secondry span {
    color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-alert--neutral.jiffyui-alert--subtile .pixel-textLink a {
    color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-alert--primary.jiffyui-alert--intense {
    background-color: var(--jiffyui-Cerulean-600);
}

.jiffyui-alert--waiting.jiffyui-alert--intense {
    background-color: var(--jiffyui-Notice-400);
}

.jiffyui-alert--negative.jiffyui-alert--intense {
    background-color: var(--jiffyui-Negative-600);
}

.jiffyui-alert--possitive.jiffyui-alert--intense {
    background-color: var(--jiffyui-Possitive-600);
}

.jiffyui-alert--neutral.jiffyui-alert--intense {
    background-color: var(--jiffyui-naturalDark-1300);
}

.jiffyui-alert--intense .jiffyui-alert_icon svg,
.jiffyui-alert--intense .jiffyui-alert_tile,
.jiffyui-alert--intense .btn.btn--secondry span,
.jiffyui-alert--intense .pixel-textLink a {
    color: var(--jiffyui-white);
}

.jiffyui-alert--intense .btn.btn--secondry {
    background-color: transparent;
    border-color: var(--jiffyui-white);
}

.jiffyui-alert--intense .jiffyui-alert_description {
    color: var(--jiffyui-naturalLight-050);
}

.jiffyui-alert-dismiss {
    position: absolute;
    right: 1rem;
    top: 1rem;
}

.jiffyui-alert-dismiss .btn.btn--tertiary {
    padding: 0.3rem;
    width: 2.5rem;
    height: 2.5rem;
    text-align: center;
    background: transparent;
    border-color: transparent;
    opacity: 0.5;
}

.jiffyui-alert-dismiss .btn.btn--tertiary:hover {
    background: #d6d6d6;
    border-color: #d6d6d6;
    opacity: 1;
}

.jiffyui-alert-dismiss .btn.btn--tertiary:hover svg {
    color: var(--jiffyui-black);
}

.jiffyui-alert-dismiss .btn.btn--tertiary .btn-icon {
    height: 100%;
    width: 100%;
}

.jiffyui-alert--intense .jiffyui-alert-dismiss .btn.btn--tertiary {
    opacity: 1;
}

.jiffyui-alert--intense .jiffyui-alert-dismiss .btn.btn--tertiary .btn-icon svg {
    color: var(--jiffyui-white);
}

.jiffyui-alert--intense .jiffyui-alert-dismiss .btn.btn--tertiary:hover {
    background: rgba(0, 0, 0, 0.1);
    border-color: transparent;
}
.btn {
    cursor: pointer;
    border-radius: var(--jiffyui-borderRadius-medium);
    font-size: 1.4rem;
    font-family: 'roboto-Medium';
    padding: 1.3rem 2rem;
    border: var(--jiffyui-borderWidth-thin) solid var(--jiffyui-black);
    transition: 0.3s all;
    position: relative;
}

.btn-icon {
    min-width: 2rem;
}

.btn-icon svg {
    width: 100%;
    display: block;
}

.btn.icononly-btn.btn--xsmall {
    padding: 0;
    width: 3rem;
}

.btn.icononly-btn.btn--small {
    padding: 0;
    width: 3.4rem;
}

.btn.icononly-btn.btn--medium {
    padding: 0;
    width: 3.8rem;
}

.btn.icononly-btn.btn--large {
    padding: 0;
    width: 4.6rem;
}

/* Button size */
.btn--xsmall {
    padding: 0.5rem 1.6rem;
    height: 3rem;
}

.btn--xsmall .btn-text {
    line-height: 1.8rem;
    font-size: 1.2rem;
}

.btn--small {
    padding: 0.7rem 1.6rem;
    height: 3.4rem;
}

.btn--small .btn-text {
    line-height: 1.8rem;
    font-size: 1.2rem;
}

.btn--medium {
    padding: 0.8rem 1.6rem;
    height: 3.8rem;
}

.btn--medium .btn-text {
    line-height: 2rem;
    font-size: 1.4rem;
}

.btn--large {
    padding: 1.2rem 1.6rem;
    height: 4.6rem;
}

.btn--large .btn-text {
    line-height: 2rem;
    font-size: 1.6rem;
}

/* primary btn */

.btn--primary {
    background: var(--jiffyui-Cerulean-600);
    border: var(--jiffyui-borderWidth-thin) solid var(--jiffyui-Cerulean-600);
    color: var(--jiffyui-white);
}

.btn--tertiary {
    border-color: var(--jiffyui-Cerulean-600);
    background-color: var(--jiffyui-white);
}

.btn--secondry {
    background-color: var(--jiffyui-tertiary-color);
    border-color: var(--jiffyui-tertiary-color);
}

.btn--secondry .btn-text,
.btn--link .btn-text,
.btn--tertiary .btn-text {
    color: var(--jiffyui-Cerulean-600);
}

.btn--link {
    padding: 0 !important;
    border: none !important;
    background-color: transparent !important;
}

/* button color */
.btn--link.btn--positive__color .btn-text {
    color: var(--jiffyui-Possitive-600);
}

.btn--primary.btn--positive__color,
.btn--secondry.btn--positive__color {
    background-color: var(--jiffyui-Possitive-600);
    border-color: var(--jiffyui-Possitive-600);
}

.btn--tertiary.btn--positive__color {
    border-color: var(--jiffyui-Possitive-600);
}

.btn--tertiary.btn--positive__color .btn-text {
    color: var(--jiffyui-Possitive-600);
}

.btn--primary.btn--negative__color,
.btn--secondry.btn--negative__color {
    background-color: var(--jiffyui-Negative-600);
    border-color: var(--jiffyui-Negative-600);
}

.btn--tertiary.btn--negative__color {
    border-color: var(--jiffyui-Negative-600);
}

.btn--tertiary.btn--negative__color .btn-text,
.btn--link.btn--negative__color .btn-text,
.btn--secondry.btn--negative__color .btn-text {
    color: var(--jiffyui-Negative-600);
}

.btn--primary.btn--waiting__color,
.btn--secondry.btn--waiting__color {
    background-color: var(--jiffyui-Notice-400);
    border-color: var(--jiffyui-Notice-400);
}

.btn--tertiary.btn--waiting__color {
    border-color: var(--jiffyui-Notice-400);
}

.btn--tertiary.btn--waiting__color .btn-text {
    color: var(--jiffyui-Notice-400);
}

.btn--link.btn--waiting__color .btn-text {
    color: var(--jiffyui-Notice-400);
}

.btn--secondry.btn--positive__color,
.btn--secondry.btn--negative__color,
.btn--secondry.btn--waiting__color {
    background-color: var(--jiffyui-tertiary-color);
    border-color: var(--jiffyui-tertiary-color);
}

.btn--secondry.btn--positive__color .btn-text {
    color: var(--jiffyui-Possitive-600);
}

.btn--secondry.btn--waiting__color .btn-text {
    color: var(--jiffyui-Notice-400);
}

.btn--primary.btn--neutral__color {
    background: var(--jiffyui-naturalDark-1300);
    border-color: var(--jiffyui-naturalDark-1300);
}

.btn--tertiary.btn--neutral__color {
    border-color: var(--jiffyui-naturalLight-300);
}

.btn--tertiary.btn--neutral__color .btn-text,
.btn--secondry.btn--neutral__color .btn-text,
.btn--link.btn--neutral__color .btn-text {
    color: var(--jiffyui-naturalDark-900);
}

/* Disabled */
button.btn.btn-disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* full width */
.btn.btn-full {
    width: 100%;
}

/* Loading */
button.btn.btn-loading {
    position: relative;
}

.btn-loading span.btn-text,
.btn-loading .btn-icon {
    opacity: 0;
}

.btn-loader__overlay {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn.btn-icon {
    display: flex;
    gap: .8rem;
    justify-content: center;
    align-items: center;
}

.iconAlign-right .btn-icon {
    order: 2;
}

.iconAlign-right .btn-text {
    order: 1;
}

.btn--primary.btn--neutral__color:hover {
    background-color: var(--jiffyui-black);
}

.btn--primary.btn--primary__color:hover {
    background-color: var(--jiffyui-Cerulean-800);
    border-color: var(--jiffyui-Cerulean-800);
}

.btn--primary.btn--negative__color:hover {
    background-color: var(--jiffyui-Negative-700);
    border-color: var(--jiffyui-Negative-700);
}

.btn--primary.btn--waiting__color:hover {
    background-color: var(--jiffyui-Notice-700);
    border-color: var(--jiffyui-Notice-700);
}

.btn--primary.btn--positive__color:hover {
    background-color: var(--jiffyui-Possitive-700);
    border-color: var(--jiffyui-Possitive-700);
}

.btn--secondry.btn--neutral__color:hover .btn-text,
.btn--tertiary.btn--neutral__color:hover .btn-text,
.btn--link.btn--neutral__color:hover .btn-text {
    color: var(--jiffyui-black);
}

.btn--secondry.btn--primary__color:hover .btn-text,
.btn--tertiary.btn--primary__color:hover .btn-text,
.btn--link.btn--primary__color:hover .btn-text {
    color: var(--jiffyui-Cerulean-800);
}

.btn--secondry.btn--negative__color:hover .btn-text,
.btn--tertiary.btn--negative__color:hover .btn-text,
.btn--link.btn--negative__color:hover .btn-text {
    color: var(--jiffyui-Negative-700);
}

.btn--secondry.btn--waiting__color:hover .btn-text,
.btn--tertiary.btn--waiting__color:hover .btn-text,
.btn--link.btn--waiting__color:hover .btn-text {
    color: var(--jiffyui-Notice-700);

}

.btn--secondry.btn--positive__color:hover .btn-text,
.btn--tertiary.btn--positive__color:hover .btn-text,
.btn--link.btn--positive__color:hover .btn-text {
    color: var(--jiffyui-Possitive-700);
}

.spinner {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    gap: 0.4rem;
}

.spinner .spinner-circle {
    animation: rotation 0.75s linear infinite;
    border-radius: 50%;
    border-left: 0.2rem solid #a3a3a3;
    border-width: 0.2rem;
    border-style: solid;
}

label.spinner-label {
    color: #6a6a6a;
}


.spinner-large .spinner-circle {
    width: 2.4rem;
    height: 2.4rem;
}

.spinner-medium .spinner-circle {
    width: 2rem;
    height: 2rem;
}

.spinner-small .spinner-circle {
    width: 1.6rem;
    height: 1.6rem;
}

label.spinner-label {
    font-size: 1.4rem;
    color: #6a6a6a;
}


.spinner-small .spinner-label {
    font-size: 1.2rem;
}

.spinner-medium .spinner-label {
    font-size: 1.3rem;
}



.spinner.spinner-small .spinner-circle {
    border-width: 0.1rem;
}


.spinner-positive .spinner-circle {
    border-color: var(--jiffyui-Possitive-800);
    border-left-color: var(--jiffyui-Possitive-600);
}

.spinner-negative .spinner-circle {
    border-color: var(--jiffyui-Negative-800);
    border-left-color: var(--jiffyui-Negative-400);
}

.spinner-notice .spinner-circle {
    border-color: var(--jiffyui-Notice-800);
    border-left-color: var(--jiffyui-Notice-400);
}

.spinner-info .spinner-circle {
    border-color: var(--jiffyui-Sapphire-800);
    border-left-color: var(--jiffyui-Sapphire-500);
}

.spinner-neutral .spinner-circle {
    border-color: var(--jiffyui-Cloud-800);
    border-left-color: var(--jiffyui-Cloud-400);
}

.spinner-primary .spinner-circle {
    border-color: var(--jiffyui-Cerulean-800);
    border-left-color: var(--jiffyui-Cerulean-500);
}

.spinner.spinner-labelRight {
    flex-direction: row;
    gap: 1rem;
}



@keyframes rotation {
    to {
        transform: rotate(360deg);
    }
}
.pixel-textLink {
    display: inline-block;
    cursor: pointer;
}

.pixel-textLink__label {
    font-size: 1.4rem;
    transition: 0.3s all;
    color: var(--jiffyui-link-color);
    display: flex;
    align-items: center;
    gap: 0.4rem
}

.pixel-textLink__label svg {
    min-width: 16px;
    display: inline-block;
    vertical-align: baseline;
    align-items: center;
}

.pixel-textLink__label:hover {
    color: var(--jiffyui-link-hover-color);
    text-decoration: underline;
}

.pixel-textLink__label:active {
    color: var(--jiffyui-link-hover-color) !important;
    text-decoration: underline;
}

/* Danger TextLink */
.pixel-textLink--negative .pixel-textLink__label {
    color: var(--jiffyui--negative-color);
    text-decoration: underline;
}

.pixel-textLink--negative .pixel-textLink__label:hover {
    color: var(--jiffyui--negative-hover-color);
    text-decoration: unset;
}

.pixel-textLink--negative .pixel-textLink__label:active {
    color: var(--jiffyui--negative-hover-color) !important;
    text-decoration: unset;
}

.pixel--textLink--disabled.pixel-textLink--negative .pixel-textLink__label,
.pixel--textLink--disabled.pixel-textLink--negative .pixel-textLink__label:hover,
.pixel--textLink--disabled.pixel-textLink--negative .pixel-textLink__label:active {
    color: var(--jiffyui--negative-hover-color) !important;
    text-decoration: unset;
    opacity: 0.4;
}

/* Danger TextLink End */
/* Warning TextLink */
.pixel-textLink--waiting .pixel-textLink__label {
    color: var(--jiffyui--warning-color);
    text-decoration: underline;
}

.pixel-textLink--waiting .pixel-textLink__label:hover {
    color: var(--jiffyui--warning-hover-color);
    text-decoration: unset;
}

